import {GetWorksView} from '@/api/class-center'
import KsImage from '@/components/ks-components/image/Image.vue'
import KsEmpty from '@/components/ks-components/empty/empty.vue'
import KsAmplification from '@/components/ks-components/amplification-img/amplification-img.vue'
export default {
	data(){
		return{
			loaded:false,
			infoid:0,
			photoData:{},
			showStatus:false,
			baseUrl:this.$store.state.SRC+"/config/app"
		}
	},
	components:{
		KsImage,
		KsEmpty,
		KsAmplification
	},
	onLoad: function(option) {
		this.infoid = option.infoid
		// uni.getSystemInfo({
		// 	success:res=>{
		// 		this.height = res.windowHeight
		// 	}
		// })
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: that.photoData.title,
			path: '/pages/class-center/photo/details?infoid='+that.infoid,
	　　　　imageUrl: that.photoData.defaultpic||this.baseUrl+'/static/images/common/default-img.png', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		setClassIndex(classid){
			this.getParameters.workclassid = classid
			this.loadPage()
		},
		getStatus(e){
			this.showStatus = e
		},
		previewImage(src,index){
			console.log(src)
			if(src){
				let tempFilePaths = []
				this.photoData.img_list.map(item=>{
					tempFilePaths.push(item.url)
				})

				// 预览图片
				uni.previewImage({
					urls: tempFilePaths,
					current:index,
					longPressActions: {
						itemList: ['发送给朋友', '保存图片', '收藏'],
						itemColor:'#fff',
						success: function(data) {
							//console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
						},
						fail: function(err) {
							console.log(err.errMsg);
						}
					},
					success:function(data){
						
					},
					fail: function(err) {
						//console.log(err.errMsg);
						uni.showToast({
							title: 'err.errMsg',
							icon:'none'
						});
					}
				});
			}else{
				uni.showToast({
					title: '没有找到图片',
					icon:'none'
				});
			}
			
		},
		loadPage(){
			GetWorksView({
				infoid:this.infoid
			}).then(res=>{
				if(res.result){
					this.photoData = res
				}
				this.loaded = true
				uni.hideLoading();
			})
			
		}
	}
}
